import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { NgZorroAntdModule } from 'ng-zorro-antd';

// 所有的子组件
import { PageHeaderComponent } from './Breadcrumb/Breadcrumb.component';
import { BaseTableComponent } from './BaseTable/BaseTable.component';
import { BaseModalComponent } from './BaseModal/BaseModal.component';
import { PaginationComponent } from './Basepagination/Basepagination.component';
import { SearchComponent } from './BaseSearch/BaseSearch.component';

// 配置公共组件
import { CommonCoModule } from 'src/app/common/common.component';


const COMPONENTS = [
  PageHeaderComponent,
  BaseTableComponent,
  BaseModalComponent,
  PaginationComponent,
  SearchComponent
];

const SHAREDS = [
  CommonCoModule,
  CommonModule,
  ReactiveFormsModule,
  FormsModule,
  RouterModule,
  NgZorroAntdModule
];

@NgModule({
  imports: [...SHAREDS],
  declarations: [...COMPONENTS],
  exports: [...COMPONENTS, ...SHAREDS]
})
export class SharedModule {}
