import {
  Component,
  OnChanges,
  OnInit,
  Output,
  Input,
  EventEmitter
} from '@angular/core';
import { BaseTable } from '../../interface/BaseTable';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
@Component({
  selector: 'app-base-table',
  templateUrl: './BaseTable.component.html',
  styleUrls: ['./BaseTable.component.less']
})
export class BaseTableComponent implements OnChanges, OnInit {
  constructor(private fb: FormBuilder) {}
  currentPage = 1;
  allChecked = false;
  dataChecked = [];
  // Parent需要把数据传输给child并在child自己的页面中显示，则需要在Child的对应 directive 标示为 input。
  @Input() tableDatas: any;
  // Output的数据流方向与input是相反的，所以那就是child控制parent的数据显示，input是parent控制child的数据显示。
  @Output() checked = new EventEmitter<object>();
  @Output() checkedSingle = new EventEmitter<object>();
  @Output() switch = new EventEmitter<object>();
  @Output() delete = new EventEmitter<object>();
  @Output() editData = new EventEmitter<object>();
  @Output() page = new EventEmitter<number>();

  ngOnInit(): void {
    this.currentPage = this.tableDatas.pageIndex;
  }
  ngOnChanges(): void {
    this.dataChange();
  }
  refreshStatus() {
    this.page.emit(this.currentPage);
  }

  dataChange() {
    if (this.tableDatas.dataSet.length !== 0) {
      if (this.tableDatas.checked || this.tableDatas.checkedSingle) {
        this.dataChecked = [];
        const lth = this.tableDatas.dataSet.length;
        for (let i = 0; i < lth; i++) {
          this.dataChecked.push({ checked: false, id: i });
        }
      }
    }
  }
  // 单选框系列的操作--------------------------
  checkAll(value: boolean): void {
    this.dataChecked.forEach(e => {
      e.checked = !e.checked;
    });
    this.checkEmit();
  }
  checkEmit(): void {
    const ids = [];
    if (this.dataChecked.some(e => e.checked) === true) {
      this.dataChecked
        .filter(e => e.checked === true)
        .forEach(ie => {
          ids.push(this.tableDatas.dataSet[ie.id]);
        });
    }

    this.checked.emit(ids);
  }
  refreSingle(i) {
    this.dataChecked
      .filter(e => e.id !== i)
      .forEach(ie => (ie.checked = false));
    this.checkedSingle.emit(i);
  }
  refrehStatus(i) {
    this.allChecked = this.dataChecked.every(e => e.checked === true);
    this.checkEmit();
    this.dataChange();
  }
  // 状态开关
  changeSwitch(e) {
    console.log(e);
  }

  edit(data) {
    this.editData.emit(data);
  }
  deletedata(data) {
    const data1 = this.tableDatas.dataSet.filter(item => {
      return item._id != data._id;
    });
    this.tableDatas.dataSet = data1;
  }
}

/**
 * itemLength :当前数据量
 * title   : 表格标题
 * footer  : 表格底部
 * loading : 加载中
 * bordered: 是否展示外边框和列边框
 * checked : 多选框
 * checkedSingle : 单选框
 * dataTh : 表头
 * dataSet : 数据
 * tdItem  : 数据项名
 * switch : 状态
 * switchName : 状态字段名
 * size : 表格大小
 * total : 数据总量 ，用于分页
 * pagination : 是否开启分页
 * pageSize : 每页数据
 * pageIndex : 当前页
 *
 */

export interface BaseTable {
  itemLength?: number;
  title?: string;
  footer?: string;
  loading?: boolean;
  bordered?: boolean;
  checked?: boolean;
  checkedSingle?: boolean;
  dataTh?: any;
  dataSet?: any;
  tdItem?: any;
  switch?: boolean;
  switchName?: string;
  size?: string;
  total?: number;
  pagination?: boolean;
  pageSize?: number;
  pageIndex?: number;
}
